import { NavLink } from "react-router-dom";
import { useHistory } from "react-router";
import { useDispatch, useSelector } from "react-redux";
import { logout } from "../../store/actions/userActions";
import homeLogo from "../../assets/icons/home.png";
import usersLogo from "../../assets/icons/users.png";
import StatisticsLogo from "../../assets/icons/increase.png";
import { AccountMenu } from "../AccountMenu";

export const AppHeader = () => {
  const loggedUser = useSelector((state) => state.userReducer.user);
  const history = useHistory();
  const dispatch = useDispatch();

  const onGoHome = () => history.push("/");

  const onLogout = () => {
    dispatch(logout()).then(() => history.push("/"));
  };

  return (
    <header className="app-header full">
      {/* <div onClick={onGoHome} className="app-logo">
        <img src={bitcoinLogo} alt="" />
      </div> */}
      <ul className="header-navbar">
        <li>
          <NavLink exact to="/" activeClassName="active-nav">
            {/* Home */}
            <img src={homeLogo} alt="" onClick={onGoHome} />
          </NavLink>
        </li>
        <li>
          <NavLink to="/contact" activeClassName="active-nav">
            <img src={usersLogo} alt="" />
            {/* Contacts */}
          </NavLink>
        </li>
        <li>
          <NavLink to="/statistic" activeClassName="active-nav">
            <img src={StatisticsLogo} alt="" />
            {/* Statistics */}
          </NavLink>
        </li>
        {/* <li>
          <NavLink to="/signup" activeClassName="active-nav">
            SignUp
          </NavLink>
        </li> */}
        <li>
          <AccountMenu
            loggedUser={loggedUser}
            logout={onLogout}
            history={history}
          />
        </li>
        {/* <AccountMenu /> */}
        {/* <li>
          {!loggedUser ? (
            <NavLink to="/signup" activeClassName="active-nav">
              SignUp
            </NavLink>
          ) : (
            <NavLink
              to="/signup"
              activeClassName="active-nav"
              onClick={onLogout}
            >
              Logout
            </NavLink>
          )}
        </li> */}
      </ul>
    </header>
  );
};
